.App {
  text-align: center;
  background-image: url('./LA-city1.jpg'); 
  background-size: cover;
}

.body{
  background-color: #282c34;
}

.HeaderBox{
  background-color: #282c34;
  opacity: .9; position: fixed; top:0;
  width: 100%;
}

a{
  text-decoration: none;
  color: white;
}

.Navigation {
  height: 10vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  margin: 0px 10vh;
  color: white;

}
.App-logo {
  height: 100vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(20px + 2vmin);

  color: white;
}

.Description{
  font-size: calc(40px + 2vmin);
}

.BtnText {
  display: block;
  color:white;
}

.BtnStart{
  background-color: #09d3ac;
  color: white;
  height: 60px; 
  width: 300px;
  border-radius: 2px;
  font-size: calc(20px + 2vmin);
  margin-top: 20px;
}

.footer{
  background-color: #1d2025;
  opacity: 1;
  width: 100%;
  max-height: 50vh;
  padding-top: 5px;
  text-decoration: none;
  color: white;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: space-evenly;
}

.footer .a{
padding: 10px;
}

.aboutsection{
  color: white;
  padding-top: 5vh;
  background-color: #09d3ac;
  height: 100vh;
  font-size: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 0px 25px;
}

.aboutsection p{
  margin-top: -30px;
  padding: 0px 20px;
  font-size: 25px;
}

.plot img {
  width: 70%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.plot p{
  padding-top: 25px;
  font-size: 30px;
}

.mainsection{
  color: white;
  padding-top: 5vh;
  background-color: chocolate;
  height: 100vh;
  font-size: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 25px;
}

.FormTable{
  margin-top: 25px; 
  font-size: 40px;
}

td, th{
  width: 50%;
}

.FormInput {
  /* This bit sets up the horizontal layout */
  display:flex;
  flex-direction:row;
  width: 600px; 
  height: 50px;
  /* This bit draws the box around it */
  border:1px solid grey;
  /* I've used padding so you can see the edges of the elements. */
  padding: 10px;
}

.FormBar {
  /* Tell the input to use all the available space */
  flex-grow: 5;
  /* And hide the input's outline, so the form looks like the outline */
  border:none;
  color: green;
  padding: 0 10px;
  font-size: 20px;
}

input:focus {
  /* removing the input focus blue box. Put this on the form if you like. */
  outline: none;
}

.FormBtn{
  /* Just a little styling to make it pretty */
  flex-grow: 1;
  border:1px solid blue;
  background:chocolate;
  color:white;
  font-size: 20px;
}